<template>
  <div class="blank">
    <div class="question-title">
      <span class="badge">{{required ? '必填' : '选填'}}</span>
      <span class="title">{{ title }}</span>
      <span class="description" v-if="description && description.replace(/\s/g, '')">（{{ description }}）</span>
    </div>
    <Form :model="model" :rules="validators">
      <FormItem prop="value">
        <xInput
          type="number"
          @on-keyup="inputChange()"
          :readonly="readonly"
          v-model="model.moneyText"
          @on-blur="blur"/>
      </FormItem>
    </Form>
  </div>
</template>

<script>
import { chineseNumber } from './util'
import validate from '@/api/validate'

export default {
  name: 'money-input',
  props: {
    schemaKey: {
      type: String,
      require: true
    },
    title: {
      type: String,
      default: ''
    },
    description: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    additionalKeys: {
      type: Array
    },
    validate: {
      type: Boolean
    },
    readonly: {
      type: Boolean
    },
    readonlyData: {
      type: Array,
      default: () => []
    },
    required: {
      type: Boolean,
      default: false
    },
    schemaType: {
      type: String
    },
    apiType: {
      type: String
    }
  },
  data () {
    return {
      uuid: '00000000-0000-0000-0000-000000000003',
      moneyText: '',
      moneyValue: '',
      moneyPreview: '',
      refuseCharacotr: /[^0-9０１２３４５６７８９千万亿,.]/g,
      unit: /([千万亿])/g,
      weightMap: {
        1000: /千/,
        10000: /万/,
        100000000: /亿/
      },
      model: {
        moneyText: ''
      },
      validators: {
        moneyText: [
          { required: this.required, message: '该项目为必填！', trigger: 'blur' }
        ]
      }
    }
  },
  mounted () {
    this.model.moneyText = this.value
    if (this.value || (!this.value && this.value == 0)) { // eslint-disable-line
      console.log('1111')
      this.inputChange(true)
    }
    // this.$emit('validator', { key: this.schemaKey, validator: this.validateForm })
  },
  methods: {
    validateForm () {
      return new Promise((resolve, reject) => {
        this.$refs.moneyForm.validate(valid => {
          if (valid) {
            resolve()
          } else {
            reject(new Error('失败'))
          }
        })
      })
    },
    formatInput () {
      this.model.moneyText = this.model.moneyText.replace(this.refuseCharacotr, '')
      this.model.moneyText = this.model.moneyText.replace(',', '')
      this.model.moneyText = this.model.moneyText.replace(/[０１２３４５６７８９]/g, function ($1) {
        return '０１２３４５６７８９'.indexOf($1)
      })
      let idx = this.model.moneyText.search(this.unit)
      if (idx !== -1) {
        for (; idx < this.model.moneyText.length; idx++) {
          if (this.model.moneyText[idx].search(this.unit)) {
            break
          }
        }
        this.model.moneyText = this.model.moneyText.slice(0, idx)
      }
      idx = this.model.moneyText.search(/[^0-9]/)
      if (idx === -1) {
        idx = this.model.moneyText.length
      }
      // for (let index = idx - 3; index > 0; index -= 3) {
      //   this.model.moneyText = this.model.moneyText.slice(0, indexgit) + ',' + this.model.moneyText.slice(index)
      // }
    },
    inputChange (first) {
      let sText = this.model.moneyText.replace(/[,，]/g, '')
      let num = 0
      let numStr = ''
      let unitStr = ''
      let idx = -1
      for (let index in sText) {
        if (isNaN(sText[index]) && sText[index] !== '.') {
          idx = index
          break
        }
      }
      if (idx !== -1) {
        numStr = sText.slice(0, idx)
        unitStr = sText.slice(idx)
        num = Number(numStr)
        for (let unit of unitStr) {
          console.log(this.getWeight(unit))
          num *= this.getWeight(unit)
        }
      } else {
        num = Number(sText)
      }
      this.numValue = num
      this.moneyPreview = chineseNumber(num)
      this.$emit('input', {
        schemaKey: this.schemaKey,
        value: String(this.numValue),
        server: this.server,
        first
      })
    },
    getWeight (charactor) {
      for (let key in this.weightMap) {
        if (charactor.search(this.weightMap[key]) !== -1) {
          return Number(key)
        }
      }
      return 0
    },
    async blur () {
      if (this.additionalKeys && this.additionalKeys.length) {
        this.$emit('additional', {
          allowClose: true,
          additionalKeys: this.additionalKeys
        })
      }

      if (this.validate) {
        const data = await validate(this.schemaType, this.apiType, {
          projectId: this.$route.query.projectId,
          valueJson: '{"' + this.schemaKey + '":"' + this.currentValue + '"}'
        })

        if (data.response.success === false && data.response.msg) {
          this.$Message.error(data.response.msg)
        } else if (data.response.success === true && data.response.msg) {
          this.$Message.info(data.response.msg)
        }
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.blank
  padding 0.1rem
  margin auto
  display flex
  flex-direction column
  font-size 1rem
  width 100%
  .question-title
    text-align left
    margin-bottom 0.5rem
    .badge
      display inline-block
      color #ffffff
      background-color #f39938
      height 24px
      line-height 24px
      width 46px
      text-align center
      border-radius 12px
      font-size 13px
    .title
      font-size 14px
      font-weight bold
      margin-left 0.5rem
    .description
      font-size 13px
      color #666
      margin-left 0.5rem
</style>
